import { Component, OnInit, ViewChild, EventEmitter, Output } from '@angular/core';
import { MaxLengthValidator } from '@angular/forms';
import { UserForm, FormServeService } from '../form-serve.service';
import { MatTable, MatTableDataSource} from '@angular/material/table';
import { MatDialog } from '@angular/material/dialog';
import { EditComponentComponent } from '../edit-component/edit-component.component';
import { Subscription } from 'rxjs';
import { MatSort } from '@angular/material/sort';
import { MatPaginator } from '@angular/material/paginator';
import { MatIconRegistry } from "@angular/material/icon";




let ELEMENT_DATA: UserForm[] = [
 
];






@Component({
  selector: 'app-new-form',
  templateUrl: './new-form.component.html',
  styleUrls: ['./new-form.component.css']
})
export class NewFormComponent implements OnInit   {

  displayedColumns: string[] = ['position', 'name', 'age', 'location','adar','pan','email','gender','qualification','about','university','action'];
  dataSource =new MatTableDataSource( ELEMENT_DATA);
  @ViewChild(MatTable,{static:true}) mytab:MatTable<any>;
 
  private reloadSub:Subscription;
  constructor(iconreg: MatIconRegistry,private ex_service :FormServeService,public dlg : MatDialog) { }
  @Output() public manaev=new EventEmitter();
  @ViewChild(MatSort) sort:MatSort;
  @ViewChild(MatPaginator) paginator:MatPaginator;
 

  ngOnInit(): void {
 
    ELEMENT_DATA=this.ex_service.getData();
    console.log(ELEMENT_DATA);
   
    this.reloadSub=this.ex_service.reloadObsservable$.subscribe((res)=>
    {if(res){this.onReload();}}
    );

      
   
  }

  ngOnDestroy(){
    this.reloadSub.unsubscribe();
  }
  onReload(){
    this.dataSource.data = this.ex_service.getData();
    console.log(this.ex_service.getData());
    console.log(this.dataSource);
    this.mytab.renderRows();
    this.dataSource.sort=this.sort;
    this.dataSource.paginator=this.paginator;


  }
  onSearch(fil : string){


    this.dataSource.filter=fil.trim().toLowerCase();
  }
  onEdit(row: UserForm){
    this.dlg.open(EditComponentComponent,{data:row});

    this.manaev.emit(row);


  }
  onDelete(row: UserForm){
    this.ex_service.delData(row);
    alert("deleted sucessfully");
    this.mytab.renderRows();
  }

}
