import { Component, OnInit, Output, EventEmitter } from '@angular/core';
import { FormBuilder ,FormArray, FormGroup, FormControl, Validators} from '@angular/forms';
import { Subscription } from 'rxjs';
import { FormServeService } from '../form-serve.service';


@Component({
  selector: 'app-form-two',
  templateUrl: './form-two.component.html',
  styleUrls: ['./form-two.component.css']
})
export class FormTwoComponent implements OnInit {


 
  @Output() public childev=new EventEmitter();
  @Output() public subev=new EventEmitter();
  
  constructor(private fb:FormBuilder,private exserve :FormServeService) {
    
     }
     private subs:Subscription;
     public count=0;
     myformtwo=this.fb.group({
      qualification:["",[Validators.required]],
      university:["",[Validators.required,Validators.minLength(4)]],
      about:["",[Validators.required,Validators.minLength(15)]],
      expertise:this.fb.array([
       this.addExp()
       
      ]),
      
      })
      
      
      
      public get qualification() {
        return this.myformtwo.value.qualification;

      }
      public get university() {
        return this.myformtwo.value.university;
        
      }
      public get about() {
        return this.myformtwo.value.about;
        
      }
    
      
      
      ngOnInit(): void {
       this.subs= this.exserve.notify$.subscribe((res)=>{
          this.myformtwo.patchValue=res;
          console.log(res);
        })
        
      }

      onResset(){
        this.myformtwo.reset();
        this.childev.emit();
        this.myformtwo.clearValidators();
        
        }

        addExp (): FormGroup{
          return this.fb.group({
             exname:["",[Validators.required]],
             sdate:["",[Validators.required]],
             edate:["",[Validators.required]],
             nxp:[]
           })
         };
         get expertise() {
          this.calcDate();
          return this.myformtwo.get('expertise') as FormArray;
        }
        onClick(){
          
          this.expertise.push(this.addExp());
        }
        
        
        calcDate(){
          let wArr=this.myformtwo.controls['expertise'].value;
          for (let index = 0; index < wArr.length; index++) {
            let edate=new Date(wArr[index].edate);
          
          let sdate=new Date(wArr[index].sdate);
         
          let yeardiff=edate.getFullYear()-sdate.getFullYear();
          this.count=yeardiff;
          wArr[index].nxp=yeardiff;
          
            
          }
          
          this.myformtwo.controls['expertise'].patchValue(wArr);
          
        }

        onSubmitt(){
          this.subev.emit(this.myformtwo.value);
          this.myformtwo.reset();
        }
        

     }




