import { Component, OnInit } from '@angular/core';
import { User } from '../user';

@Component({
  selector: 'app-indxp',
  templateUrl: './indxp.component.html',
  styleUrls: ['./indxp.component.css']
})
export class IndxpComponent implements OnInit {

  genderarr=['Male','Female'];
  vuser=new User("","","",false,"");
  constructor() { }

  ngOnInit(): void {

    console.log("Index Component Initialized");
  }
  isDone() : boolean{
    return !this.vuser.accept;
  }

  isSame() : boolean{
    console.log(this.vuser.password);
    console.log(this.vuser.cpassword);
    if(this.vuser.password==this.vuser.cpassword){return false}else return true;
  }
  Submit():void{
    alert("Form Successfully Submitted for ,\n Email : "+this.vuser.email+",\nPassword is : "+this.vuser.password+"\nGender:"+this.vuser.gender);
  }
  Cancel(){
    location.reload();
  }
  isDef():boolean{
    let x= this.vuser.gender;
    if(x==""){
      return true;
    }else return false;
  
  }

}
