import { Component, OnInit, ViewChild } from '@angular/core';
import { FormBuilder, Validators, FormGroupDirective, NgForm } from "@angular/forms";
import { Expertise,UserForm,FormServeService } from '../form-serve.service';
import { Subscription } from 'rxjs';


@Component({
  selector: 'app-form-one',
  templateUrl: './form-one.component.html',
  styleUrls: ['./form-one.component.css']
})
export class FormOneComponent implements OnInit {

  private count=0;
  @ViewChild('formDirective') private formDirective : NgForm;

  constructor(private fb : FormBuilder,private ex_service : FormServeService) { }

  ngOnInit(): void {
    
  }
myformone=this.fb.group({
  name:["",[Validators.required,Validators.minLength(4)]],
  position:[],
  age:["",[Validators.required,Validators.max(100),]],
  location:["",[Validators.required,Validators.minLength(3)]],
  adar:["",[Validators.required,Validators.minLength(12)]],
  email:["",[Validators.required,Validators.email]],
  pan:["",[Validators.required,Validators.minLength(8)]],
  gender:["",]
})


get name(){
  return this.myformone.value.name;
}
get position(){
  return this.myformone.value.position;
}
get age(){
  return this.myformone.value.age;
}
get location(){
  return this.myformone.value.location;
}
get adar(){
  return this.myformone.value.adar;
}
get email(){
  return this.myformone.value.email;
}
get pan(){
  return this.myformone.value.pan;
}
get gender(){
  return this.myformone.value.gender;
}

public myformtw :FormTwo;
public wForm :UserForm;
public oform:FormOne;

onResset(){
  
  this.myformone.reset();
  this.formDirective.resetForm();
this.myformone.clearAsyncValidators();
  this.myformone.markAsUntouched;
  this.myformone.markAsPristine;
  this.myformone.clearValidators();
}
onSubmitt(myvara : FormTwo){
  this.oform=this.myformone.value;
  
  this.oform.position=this.count++;
  
  this.ex_service.addData({name:this.oform.name
                          ,position:this.oform.position,
                          age:this.oform.age,
                          location:this.oform.location,
                          adar:this.oform.adar,
                          email:this.oform.email,
                          gender:this.oform.gender,
                          pan:this.oform.pan,
                          qualification:myvara.qualification,
                          about:myvara.about,
                          university:myvara.university,
                          expertise:myvara.expertise});

                          alert("Form Submitted Sucessfully");

  this.onResset();
  this.ex_service.reloadTab({opt:"Sucessfully reloaded table"});
}
onEdit(row :UserForm){

  this.myformone.patchValue({name:row.name,position:row.position,age:row.age,location:row.location,adar:row.adar,email:row.email,gender:row.gender,pan:row.pan});
  this.myformtw={qualification:row.qualification,about:row.about,university:row.university,expertise:row.expertise};
  this.ex_service.notifyChild({qualification:row.qualification,about:row.about,university:row.university,expertise:row.expertise});
}


}



export interface FormTwo{

  qualification:string[];
  university:string;
  about:string;
  expertise:Expertise[];



}
export interface FormOne{
  name:string,
  position:number,
  age:number,
  location:string,
  adar:number,
  email:string,
  pan:string,
  gender:string
}

