import { Component, OnInit, Injector, Inject } from '@angular/core';
import { FormBuilder, FormGroup, FormArray, Validators } from '@angular/forms';
import { FormServeService ,Expertise,UserForm} from '../form-serve.service';import{MAT_DIALOG_DATA} from '@angular/material/dialog'


@Component({
  selector: 'app-edit-component',
  templateUrl: './edit-component.component.html',
  styleUrls: ['./edit-component.component.css']
})
export class EditComponentComponent implements OnInit {

  constructor(private fb : FormBuilder,private ex_service : FormServeService,@Inject(MAT_DIALOG_DATA) public data :any) { }

  ngOnInit(): void {
    this.myform.setValue(this.data);
  }

toggle=false;
  myform=this.fb.group({
    name:["",[Validators.required,Validators.minLength(4)]],
    position:[],
    age:["",[Validators.required,Validators.max(100),]],
    location:["",[Validators.required,Validators.minLength(3)]],
    adar:["",[Validators.required,Validators.minLength(12)]],
    email:["",[Validators.required,Validators.email]],
    pan:["",[Validators.required,Validators.minLength(8)]],
    gender:["",],
   
    qualification:["",[Validators.required]],
    university:["",[Validators.required,Validators.minLength(4)]],
    about:["",[Validators.required,Validators.minLength(15)]],
    expertise:this.fb.array([
     this.addExp()
     
    ]),
    
  })
  

  addExp (): FormGroup{
    return this.fb.group({
      exname:["",[Validators.required]],
      sdate:["",[Validators.required]],
      edate:["",[Validators.required]],
      nxp:[]
     })
   };
   get expertise() {
    this.calcDate();
    return this.myform.get('expertise') as FormArray;
  }
  onClick(){

    this.expertise.push(this.addExp());
  }
  calcDate(){
    let wArr=this.myform.controls['expertise'].value;
    for (let index = 0; index < wArr.length; index++) {
      let edate=new Date(wArr[index].edate);
    
    let sdate=new Date(wArr[index].sdate);
   
    let yeardiff=edate.getFullYear()-sdate.getFullYear();
    
    wArr[index].nxp=yeardiff;
    
      
    }
    
    this.myform.controls['expertise'].patchValue(wArr);
    
  }
  onResset(){
    this.myform.reset;

  }
  onEdit(){
    this.ex_service.editData({name:this.myform.value.name
      ,position:this.myform.value.position,
      age:this.myform.value.age,
      location:this.myform.value.location,
      adar:this.myform.value.adar,
      email:this.myform.value.email,
      gender:this.myform.value.gender,
      pan:this.myform.value.pan,
      qualification:this.myform.value.qualification,
      about:this.myform.value.about,
      university:this.myform.value.university,
      expertise:this.myform.value.expertise});

      this.toggle=true;
      this.ex_service.reloadTab({name:"Asset"});

  }

}
