import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { IndxpComponent } from './indxp/indxp.component';
import { FormOneComponent } from './form-one/form-one.component';
import { FormTwoComponent } from './form-two/form-two.component';
import { NewFormComponent } from './new-form/new-form.component';




const routes: Routes = [
  {path:'',redirectTo:'index',pathMatch:'full'},
{path:'index',component:IndxpComponent},
{path:'formone',component:FormOneComponent},
{path:'formtwo',component:FormTwoComponent},
{path:'new',component:NewFormComponent}


];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }

export const routingComponents=[IndxpComponent]