import { Injectable } from '@angular/core';
import { Subject } from 'rxjs';
import { FormTwo } from './form-one/form-one.component';

@Injectable({
  providedIn: 'root'
})

export class FormServeService {

  constructor() { }
  d1=new Date();
  d2=new Date();


   static ulist: UserForm[]=[] ;
  /**
   * name
   */
  
  public getData():UserForm[] {
    console.log(FormServeService.ulist);
    return FormServeService.ulist;
    
  }
  
  public editData(usr : UserForm,){
    for (let index = 0; index < FormServeService.ulist.length; index++) {
      if (usr.position==FormServeService.ulist[index].position){
        FormServeService.ulist[index]=usr;
      }

   
  }}
   public addData(usr :UserForm){
    console.log(usr);
    FormServeService.ulist[usr.position]=usr;
    console.log(FormServeService.ulist);
  }
  public delData(usr : UserForm){
    for (let index = 0; index < FormServeService.ulist.length; index++) {
      if (usr.name==FormServeService.ulist[index].name){
        FormServeService.ulist.splice(index,1);
      }
      
    }
       

     

  }
  /**
   * notifyChild
   */
  public notifyChild(row : FormTwo) {
    if(row){
      alert(row.about+row.expertise);
      this.notify$.next(row)
    }else{alert("Null")}
    
  }
  public reloadTab(row : any) {
    if(row){
      this.reload.next(row);
    }
    
  }

   notify$=new Subject<any>();
   private reload =new Subject<any>();
   reloadObsservable$=this.reload.asObservable();


}


export interface UserForm {
  name: string;
  position: number;
  age: number;
  location: string;
  adar:number;
  email:string;
  gender:string;
  pan:string;
  qualification:string[];
  university:string;
  about:string;
  expertise:Expertise[];


}
export interface Expertise{
  exname:string;
  sdate:Date;
  edate:Date;
  nxp:number;
}